/* default played style, the Karaoke component will instrument the font-weight */
.transcript {
  font-family: source-code-pro, Monaco, Menlo, Consolas, 'Courier New', monospace;
  font-size: 12px;
  font-weight: bold;
}

/* show paragraph timecodes */
.transcript.timecodes-true p::before {
  display: block;
  font-size: 9px;
  font-weight: normal;
  content: attr(data-tc);
}

.transcriptWrapper h4 {
  margin: 5px 10px 0 0 !important;
}

